.footer {
    font-family: 'Pacifico';
    color: white;
    padding-bottom: 2rem;
}

@media (prefers-reduced-motion: no-preference) {
  .footer-logo {
    animation: footer-logo-spin infinite 5s linear;
  }
}
  
@keyframes footer-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}